<template>
  <div>
    <el-breadcrumb separator-icon="ArrowRight" style="margin: 16px">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>个人信息</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card style="margin: 15px;min-height:calc(100vh - 111px);">
      <div style="display: flex;">
        <div style="width: 600px;margin-left: 300px;position: relative">
          <div>
            <el-upload
                :on-success="uploadSuccess"
                :show-file-list="false"
                action="http://localhost:9090/files/upload/"
                class="upload-demo"
            >
              <div class="AvatarDiv">
                <el-avatar icon="UserFilled" style="width: 80px;height: 80px;"></el-avatar>
                <img :src="'data:image;base64,'+image" :style="imgDisplay"
                     style="width: 80px;height: 80px;border-radius: 40px;">
                <div class="editImg">
                  更换头像
                  <el-icon color="color">
                    <edit></edit>
                  </el-icon>
                </div>
              </div>
            </el-upload>
          </div>
          <el-descriptions
              ref="descript"
              :column="1"
              border
              class="margin-top"
              style="min-width: 500px;"
              title=""
          >
            <el-descriptions-item prop="username">
              <template #label>
                <div class="cell-item">
                  <el-icon >
                    <user/>
                  </el-icon>
                  用户名
                </div>
              </template>
              {{ username }}
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label prop="username">
                <div class="cell-item">
                  <el-icon >
                    <location/>
                  </el-icon>
                  姓名
                </div>
              </template>
              {{ name }}
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  <el-icon >
                    <tickets/>
                  </el-icon>
                  性别
                </div>
              </template>
              <!--              <el-tag size="small">School</el-tag>-->
              {{ gender }}
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  <el-icon >
                    <office-building/>
                  </el-icon>
                  年龄
                </div>
              </template>
              {{ age }}
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  <el-icon >
                    <iphone/>
                  </el-icon>
                  手机号
                </div>
              </template>
              {{ phoneNum }}
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  <el-icon >
                    <office-building/>
                  </el-icon>
                  邮箱
                </div>
              </template>
              {{ email }}
            </el-descriptions-item>
          </el-descriptions>
          <el-tooltip content="修改信息" placement="bottom">
            <el-button icon="Edit"
                       size='large'
                       style="margin-top: 30px;
                       width: 80px"
                       type="primary"
                       @click="Edit"
            >
            </el-button>
          </el-tooltip>
        </div>
      </div>
      <div>
        <!--      弹窗-->
        <el-dialog
            v-model="dialogVisible"
            title="操作"
            width="30%"
            @close='cancel'
        >
          <el-form ref="form" :model="form" :rules="rules" label-width="120px">
            <el-form-item label="账号" prop="username">
              <el-input v-model="form.username" disabled style="width: 80%"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input v-model="form.password" :disabled="disabled" :show-password="showpassword" show-password
                        style="width: 80%"></el-input>
              <el-tooltip content="修改密码" placement="right">
                <el-icon size="large" style="margin-left: 5px;cursor: pointer" @click="EditPass">
                  <edit/>
                </el-icon>
              </el-tooltip>
            </el-form-item>
            <el-form-item :style="display" label="确认密码" prop="checkPass">
              <el-input v-model="form.checkPass" show-password style="width: 80%"></el-input>
            </el-form-item>
            <el-form-item label="姓名" prop="name">
              <el-input v-model="form.name" style="width: 80%"></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="gender">
              <el-radio v-model="form.gender" label="男">男</el-radio>
              <el-radio v-model="form.gender" label="女">女</el-radio>
            </el-form-item>
            <el-form-item label="年龄" prop="age">
              <el-input v-model.number="form.age" style="width: 80%"></el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="phoneNum">
              <el-input v-model.number="form.phoneNum" style="width: 80%"></el-input>
            </el-form-item>
            <el-form-item label="邮箱地址" prop="email">
              <el-input v-model="form.email" style="width: 80%"></el-input>
            </el-form-item>
          </el-form>
          <template #footer>
          <span class="dialog-footer">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click='save'>确 定</el-button>
          </span>
          </template>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>

<script>
import request from "@/utils/request";

const {ElMessage} = require("element-plus");
export default {
  name: "selfInfo",
  data() {
    // 手机号验证
    const checkPhone = (rule, value, callback) => {
      const phoneReg = /^1[3|4|5|6|7|8][0-9]{9}$/
      if (!value) {
        return callback(new Error('电话号码不能为空'))
      }
      setTimeout(() => {

        if (!Number.isInteger(+value)) {
          callback(new Error('请输入数字值'))
        } else {
          if (phoneReg.test(value)) {
            callback()
          } else {
            callback(new Error('电话号码格式不正确'))
          }
        }
      }, 100)
    };
    const checkPass = (rule, value, callback) => {
      if (!this.editJudge) {
        console.log('验证')
        if (value == '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.form.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      } else {
        console.log('不验证')
        callback();
      }
    };
    return {
      showpassword: true,
      image: '',
      editJudge: true,
      disabled: true,
      dialogVisible: false,
      identity: '',
      username: '',
      name: '',
      gender: '',
      age: '',
      phoneNum: '',
      email: '',
      targetURL: '',
      avatar: '',
      form: {
        username: '',
        name: '',
        gender: '',
        age: '',
        phoneNum: '',
        email: '',
      },
      rules: {
        username: [
          {required: true, message: '请输入学号', trigger: 'blur'},
          {pattern: /^[a-zA-Z0-9]{4,9}$/, message: '必须由 4 到 9 个字母或数字组成', trigger: 'blur'}
        ],
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'},
          {pattern: /^(?:[\u4E00-\u9FA5·]{2,10})$/, message: '必须由 2 到 10 个汉字组成', trigger: 'blur'}
        ],
        gender: [
          {required: true, message: '请选择性别', trigger: 'change'},
        ],
        age: [
          {required: true, message: '请输入年龄', trigger: 'blur'},
          {type: 'number', message: '年龄必须为数字值', trigger: 'blur'},
          {pattern: /^(1|[1-9]\d?|100)$/, message: '范围：1-100', trigger: 'blur'}
        ],
        phoneNum: [
          {required: true, validator: checkPhone, trigger: 'blur'}
        ],
        email: [
          {type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, max: 32, message: '长度在 6 到 16 个字符', trigger: 'blur'},
        ],
        checkPass: [
          {validator: checkPass, trigger: 'blur'}
        ],
      },
      display: {
        display: 'none',
      },
      imgDisplay: {
        display: 'none',
      }
    }
  },
  created() {
    this.load()
    this.find()
    this.init(this.avatar)
  },
  methods: {
    //获取个人信息页面信息
    load() {
      this.form = JSON.parse(sessionStorage.getItem("user"));
      this.identity = JSON.parse(sessionStorage.getItem("identity"));
      this.username = this.form.username
      this.name = this.form.name
      this.gender = this.form.gender
      this.age = this.form.age
      this.phoneNum = this.form.phoneNum
      this.email = this.form.email
      this.avatar = this.form.avatar
    },
    //查询数据，更新session
    find() {
      this.form = JSON.parse(sessionStorage.getItem("user"))
      request.post("/" + this.identity + "/login", this.form).then(res => {
        //更新sessionStorage
        window.sessionStorage.setItem("user", JSON.stringify(res.data))
        //更新页面数据
        this.load()
      })
    },
    Edit() {
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.form.resetFields();
        this.form = JSON.parse(sessionStorage.getItem("user"))
      })
    },
    cancel() {
      this.$refs.form.resetFields();
      this.display = {display: 'none'}
      this.showpassword = true
      this.editJudge = true;
      this.disabled = true
      this.dialogVisible = false
    },
    async save() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          //修改
          await request.put("/" + this.identity + "/update", this.form).then(res => {
            if (res.code === '0') {
              ElMessage({
                message: '修改成功',
                type: 'success',
              })
              //更新sessionStorage
              window.sessionStorage.setItem("user", JSON.stringify(this.form))
              this.find()
              this.dialogVisible = false
            } else {
              ElMessage({
                message: res.msg,
                type: 'error',
              })
            }
          })
        }
      })
    },
    EditPass() {
      if (this.editJudge) {
        this.display = {display: 'flex'}
        this.showpassword = false
        this.disabled = false
        this.editJudge = false;
      } else {
        this.display = {display: 'none'}
        this.showpassword = true
        this.editJudge = true;
        this.disabled = true
      }
    },
    //发送请求，获取头像
    async init(data) {
      if (data === null || data === '') {
        console.log("用户未设置头像")
        this.imgDisplay = {display: 'none'}
      } else {
        this.imgDisplay = {display: 'block'}
        console.log("头像名称：" + data)
        await request.get("/files/initAvatar/" + data).then(res => {
          if (res.code === '0') {
            this.image = res.data.data
          } else {
            ElMessage({
              message: res.msg,
              type: 'error',
            })
          }
        })
      }
    },
    async uploadSuccess() {
      this.form = JSON.parse(sessionStorage.getItem("user"));
      await request.post("/files/uploadAvatar/" + this.identity, this.form).then(res => {
        if (res.code === '0') {
          ElMessage({
            message: '设置成功',
            type: 'success',
          })
          //获取头像文件名
          this.avatar = res.data
          console.log("上传成功：" + this.avatar)
          this.find()
          this.init(this.avatar)
        } else {
          ElMessage({
            message: res.msg,
            type: 'error',
          })
        }
      })
    }
  }
}
</script>

<style scoped>
.editImg {
  width: 78px;
  height: 78px;
  border-radius: 39px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 14px;
  line-height: 79px;
  color: #cccccc;
  position: absolute;
  left: 261px;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
}

.AvatarDiv {
  width: 80px;
  height: 80px;
  border-radius: 40px;
  display: flex;
  margin: 0 auto;
}

.AvatarDiv:hover .editImg {
  display: block;
  cursor: pointer;
}

.upload-demo {
  display: grid;
  width: 80px;
  height: 80px;
  margin: 40px auto;
}

</style>